<script>
// import Schart from 'vue-schart';
// import bus from '../common/bus';
import Qs from "qs";
export default {
    name: 'dashboard',
    data() {
        return {
            name:  sessionStorage.getItem('my_username'),
            photo:"",
            // todoList:this.$store.state.home.todoList,
            waitWorkList:[],
            todoList: [
                {
                    title: '今天要修复100个bug',
                    status: false
                },
                {
                    title: '今天要修复100个bug',
                    status: false
                },
                {
                    title: '今天要写100行代码加几个bug吧',
                    status: false
                },
                {
                    title: '今天要修复100个bug',
                    status: false
                },
                {
                    title: '今天要修复100个bug',
                    status: true
                },
                {
                    title: '今天要写100行代码加几个bug吧',
                    status: true
                }
            ],
            data: [
                {
                    name: '2018/09/04',
                    value: 1083
                },
                {
                    name: '2018/09/05',
                    value: 941
                },
                {
                    name: '2018/09/06',
                    value: 1139
                },
                {
                    name: '2018/09/07',
                    value: 816
                },
                {
                    name: '2018/09/08',
                    value: 327
                },
                {
                    name: '2018/09/09',
                    value: 228
                },
                {
                    name: '2018/09/10',
                    value: 1065
                }
            ],
            options: {
                type: 'bar',
                title: {
                    text: '最近一周各品类销售图'
                },
                xRorate: 25,
                labels: ['周一', '周二', '周三', '周四', '周五'],
                datasets: [
                    {
                        label: '家电',
                        data: [234, 278, 270, 190, 230]
                    },
                    {
                        label: '百货',
                        data: [164, 178, 190, 135, 160]
                    },
                    {
                        label: '食品',
                        data: [144, 198, 150, 235, 120]
                    }
                ]
            },
            options2: {
                type: 'line',
                title: {
                    text: '最近几个月各品类销售趋势图'
                },
                labels: ['6月', '7月', '8月', '9月', '10月'],
                datasets: [
                    {
                        label: '家电',
                        data: [234, 278, 270, 190, 230]
                    },
                    {
                        label: '百货',
                        data: [164, 178, 150, 135, 160]
                    },
                    {
                        label: '食品',
                        data: [74, 118, 200, 235, 90]
                    }
                ]
            }
        };
    },
    components: {
        // Schart
    },
    computed: {
        role() {
            return sessionStorage.getItem('my_purview') === 'manger' ? '超级管理员' : '普通用户';
        }
    },
    created(){    
            // this.flag = this.$store.state.home.flag;
            let id = sessionStorage.getItem('my_id');
            let my_purview = sessionStorage.getItem('my_purview')
            this.id = id;
            this.$axios.get("/waitwork",{params:{
                id:id
            }}).then((res)=>{
                let waitWorkList = res.data.waitwork;
                for(let i = 0;i<this.waitWorkList.length;i++){
                    if(waitWorkList[i].status == 0){
                        waitWorkList[i].status = false
                    }else{
                        waitWorkList[i].status = true;
                    }
                }
                // if(this.flag){
                    this.$store.state.home.waitWorkList =  waitWorkList;
                    this.$store.state.home.flag = false;
                // }

                this.waitWorkList = this.$store.state.home.waitWorkList
                // console.log(this.waitWorkList);
            });
            
            let data = {
                my_id: id,
                my_purview:my_purview
            };

            this.$axios.post("/getmyinfo", Qs.stringify(data)).then(res => {
                // console.log(res.data[0]);
                // this.form = res.data[0];
                let urlTemp = res.data[0].photo;
                // console.log(typeof(urlTemp))
                this.photo = "/img" + urlTemp;
            });



    },
    destroyed(){
        // let id = sessionStorage.getItem('my_id');
        // this.$axios.get("/updatework",{params:{
        //         id:id,
        //         waitwork:this.waitwork
        //     }})
    },
    methods: {
        changeDate() {
            const now = new Date().getTime();
            this.data.forEach((item, index) => {
                const date = new Date(now - (6 - index) * 86400000);
                item.name = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
            });
            
        },
        checkList(){
            // let list = this.$store.state.home.waitWorkList
            // console.log(list)
        },
        open() {
            let my_purview = sessionStorage.getItem('my_purview');
            this.$prompt('请输入待办事项', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
            // inputErrorMessage: '邮箱格式不正确'
            }).then(({ value }) => {
                // console.log(value)
                this.waitWorkList.unshift({title:value,status:false})
                this.$message({
                    type: 'success',
                    message: '添加成功'
                });
                //添加入数据库中
                let id = this.id;
                this.$axios.get("/addwait",{params:{
                    id:id,
                    value:value,
                    my_purview:my_purview,
                }})

            }).catch(() => {
            this.$message({
                type: 'info',
                message: '取消输入'
            });       
            });
        },
        onEdit(index){
            // console.log(index);
            this.$prompt('请修改', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
            // inputErrorMessage: '邮箱格式不正确'
            }).then(({ value }) => {
                // console.log(value)
                let oldvalue = this.waitWorkList[index].title;
                this.waitWorkList[index].title = value;
                this.$message({
                    type: 'success',
                    message: '修改成功'
                });
                let id = this.id
                this.$axios.get("/editwait",{params:{
                    id:id,
                    value:value,
                    oldvalue
                }})

            }).catch(() => {
            this.$message({
                type: 'info',
                message: '取消'
            });       
            });
            // console.log(item)

        },
        onDel(index){
            let delvalue = this.waitWorkList[index].title;
            let id = this.id
            this.waitWorkList.splice(index,1);
            this.$axios.get("/delwait",{params:{
                    id:id,
                    delvalue
            }})


        }
        
    },
    
};
</script>


