<template>
    <div style="padding:20px;">
        <div class="main_container">
            <h1 class="title">病理信息管理</h1>

            <div class="search">
                <el-input
                    placeholder="请输入内容"
                    prefix-icon="el-icon-search"
                    v-model="search"
                    clearable
                ></el-input>
            </div>

            <div class="table_container">
                <el-table
                    :data="sicknessList.filter(data => !search || data.Sname.toLowerCase().includes(search.toLowerCase())  || data.Sid.toString().toLowerCase().includes(search.toLowerCase()) )"
                    style="width: 100%"
                >
                    <el-table-column type="expand">
                        <template slot-scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                                <el-form-item label="病理信息编号:">
                                    <span>{{ props.row.Tid }}</span>
                                </el-form-item>
                                <el-form-item label="患者id:">
                                    <span>{{ props.row.Sid }}</span>
                                </el-form-item>
                                <el-form-item label="姓名:">
                                    <span>{{ props.row.Sname }}</span>
                                </el-form-item>
                                <el-form-item label="过敏史:">
                                    <span>{{ props.row.passillness }}</span>
                                </el-form-item>
                                <el-form-item label="其他:">
                                    <span>{{ props.row.other }}</span>
                                </el-form-item>

                                <el-form-item label="负责医生编号:">
                                    <span>{{ props.row.author }}</span>
                                </el-form-item>
                                
                                
                                <el-form-item label="组织检查报告:">
                                    <!-- <el-image
                                        style="width: 100px; height: 100px"
                                        :src="props.row.photo"
                                        :preview-src-list="[props.row.photo]"
                                    ></el-image> -->
                                    <el-button type="primary" @click="downloadCodeImg(props.$index)" size="small" plain>点击下载</el-button>
                                    <!-- <span @click="downloadCodeImg(props.$index)">点击下载</span> -->
                                    <!-- <a href="../../assets/img/a1.png" download="w3logo">点击下载</a> -->
                                    <!-- <img src="/img/a1.png" alt=""> -->
                                </el-form-item>
                                <el-form-item label="添加时间:">
                                        <span>{{ props.row.updatatime }}</span>
                                </el-form-item>
                                <el-form-item label="诊断结果:" class="result">
                                        <span>{{ props.row.sickness }}</span>
                                </el-form-item>
                            </el-form>
                        </template>
                    </el-table-column>
                    <el-table-column label="病理信息ID" prop="Tid"></el-table-column>
                    <el-table-column label="患者ID" prop="Sid"></el-table-column>
                    <el-table-column label="患者性名" prop="Sname"></el-table-column>

                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <!-- <el-button size="mini" type="info">详情</el-button> -->
                            <el-button size="mini" @click="onEdit(scope.$index, scope.row)">修改</el-button>
                            <el-button
                                size="mini"
                                type="danger"
                                @click="onDel(scope.$index, scope.row)"
                            >删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <div class="bar" v-if="showFlag">
                <div class="top">
                    <i class="el-icon-close" @click="onClose"></i>
                </div>
                <div class="bar_content">
                    <el-form class="clearfix">
                        <el-form-item label="病理信息编号:">
                                    <el-input v-model="editList.Tid" readonly></el-input>
                                </el-form-item>
                                <el-form-item label="患者id:">
                                    <el-input v-model="editList.Sid" readonly></el-input>
                                </el-form-item>
                                <el-form-item label="姓名:">
                                    <el-input v-model="editList.Sname" readonly></el-input>
                                </el-form-item>
                                <el-form-item label="过敏史:">
                                    <el-input v-model="editList.passillness"></el-input>
                                </el-form-item>
                                <el-form-item label="其他:">
                                    <el-input v-model="editList.other"></el-input>
                                </el-form-item>

                                <!-- <el-form-item label="负责医生编号:">
                                    <el-input v-model="editList.author"></el-input>
                                </el-form-item> -->
                                <el-form-item label="组织检查报告:" class="photo_item"> 
                                    <!-- <el-image
                                        style="width: 100px; height: 100px"
                                        :src="editList.photo"
                                        :preview-src-list="[editList.photo]"
                                    ></el-image> -->

                                   <el-upload 
                                        class="upload-demo" 
                                        :auto-upload="false" 
                                        action="111"
                                        :limit="1" 
                                        ref="upload"
                                        :http-request="upload"
                                        multiple>
                                        <!-- :http-request="upload" -->
                                        <el-button size="small" type="primary">重新上传</el-button>
                                        <!-- <div slot="tip" class="el-upload__tip">只能上传pdf文件</div> -->
                                    </el-upload>
                                    
                                
                                </el-form-item>
                                
                                <el-form-item label="诊断结果:" class="photo_item">
                                    <el-input type="textarea" :rows="3" :autosize="{ minRows: 1, maxRows: 3 }" v-model="editList.sickness"></el-input>
                                </el-form-item>
                                
                    </el-form>
                </div>

                <div class="bar_bottom">
                    <el-button class="sureEdit" size="mini" type="success" @click="upload">确认修改</el-button>
                    <el-button class="sureEdit" size="mini" @click="onClose">取消</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Qs from "qs";
export default {
    data() {
        return {
            editList:{},
            sicknessList: [],
            showFlag: false,
            search: "",
            fileList:[]
        };
    },
    methods: {
        upload() {
            const formData = new FormData();
            const file = this.$refs.upload.uploadFiles[0];
            const headerConfig = { headers: { 'Content-Type': 'multipart/form-data' } };
            if (file) { // 若未选择文件                
                formData.append('file', file.raw);               
            }
            formData.append('Tid', this.editList.Tid);
            formData.append('Sid', this.editList.Sid);
            formData.append('Sname', this.editList.Sname);
            formData.append('passillness', this.editList.passillness);
            formData.append('other', this.editList.other);
            formData.append('sickness', this.editList.sickness);
            this.$axios.post('/api/upload', formData, headerConfig).then((res) => {
                // console.log(res.data)
                let index =  this.sicknessList.findIndex((item) => item.Tid == this.editList.Tid);
                // console.log(index)
                this.sicknessList[index] = res.data[0]
                // console.log(this.sicknessList[index])
                this.showFlag = false;
            })
        },
        onDel(index, row) {
            this.$confirm("此操作将删除该用户, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "删除成功!"
                    });

                    // let _this = this;
                    // console.log(row);
                    this.sicknessList.splice(index, 1);

                    //删除接口
                    this.$axios.get("/delsickness", {
                        params: {
                            Tid: row.Tid
                        }
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        },
        onEdit(index) {
            this.showFlag = true;
            // this.editList = this.sicknessList[index];
            this.editList = JSON.parse(JSON.stringify(this.sicknessList[index]))
            // console.log(this.sicknessList[index]);
        },
        sureEdit() {
            let index = this.sickerList.findIndex(
                item => item.Sid == this.editList.Sid
            );

            this.sickerList.splice(index, this.editList);
            // console.log(index);
            this.showFlag = false;
            let _this = this;
            let data = this.editList;
            this.$axios.post("/editsicker", Qs.stringify(data)).then(() => {
                _this.$message({
                    message: `修改成功`,
                    type: "success"
                });
            });
        },
        onClose() {
            this.showFlag = false;
            this.editList =null;
            // console.log(this.editList)

        },
        downloadCodeImg(index) {
            // console.log(index);
            var a = document.createElement("a");
            a.download = name || this.sicknessList[index].Sname + "检查报告"+this.sicknessList[index].Tid; // 设置图片地址
            // a.href = "E:/工具/Hbuilder/病理信息2/project3/src/assets/img" + this.sicknessList[index].tgjc;
            a.href = "/img"+this.sicknessList[index].tgjc;
            a.click();
        }
    },
    created() {
        let data = {
            my_id: sessionStorage.getItem("my_id")
        };

        if ("path" in this.$route.query) {
            // path = _this.$route.query.path;
            this.search = this.$store.state.sick.checksickness[0].Sname;            
        }

        this.$axios.post("/getsickness", Qs.stringify(data)).then(res =>{
            // console.log(res.data);
            this.sicknessList = res.data;
            // console.log(this.sicknessList);

            for (var i = 0; i < this.sicknessList.length; i++) {
                let photo = "/img" +this.sicknessList[i].tgjc;
                this.sicknessList[i].photo = photo;
            }

            // this.photo = require("../../assets/img" + urlTemp);
        });
    }
};
</script>

<style lang="scss" scoped>
.clearfix::after {
    content: "";
    display: block;
    clear: both;
}

.title {
    height: 30px;
    line-height: 30px;
    font-size: 25px;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: 30px;
}
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
.table_container .demo-table-expand .result{
    width: 100%;
}
// 控制弹出框
.main_container {
    position: relative;
}
.bar {
    position: fixed;
    left: 345px;
    top: 90px;
    width: 800px;
    height: 460px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 10px;
    z-index: 99;
    // box-shadow:;

    .top {
        height: 20px;

        i {
            float: right;
            font-size: 20px;
            margin-top: 10px;
            margin-right: 10px;
            cursor: pointer;
        }
    }

    .bar_content {
        // width: 100%;
        padding: 20px;
        .el-form-item {
            float: left;
            margin-right: 52px;
            margin-bottom: 0px;
            // height: 30px;
        }
        .el-input {
            width: 200px;
            height: 30px;
        }

        .el-input__inner {
            height: 30px;
        }
    }

    .bar_bottom {
        text-align: center;
    }
    .el-button .sureEdit {
        height: 30px;
        width: 100px;
    }
}


.photo_item{
    width:100%;
    margin-top: 20px;
}

.back{
    height:30px;
}
</style>