        <div>
            <div class="form-box clearfix">
                <!-- <el-form
                    :model="ruleForm"
                    method="POST" action="/adddata" enctype="multipart/form-data"
                    status-icon
                    :rules="rules"
                    ref="ruleForm"
                    label-width="100px"
