                        <el-input type="text" v-model="ruleForm.other" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item class="max_item" label="组织检查报告">                       
                         <input type="file" name="photo" @change="getFile($event)" multiple="multiplt">
                    </el-form-item>

                    <el-form-item class="max_item" label="诊断结果">
                        <el-input
                            class="textarea"
                            type="textarea"
                            :autosize="{ minRows: 7, maxRows: 4}"
                            placeholder="请输入内容"
                            v-model="ruleForm.result">
                            </el-input>
                        
                    </el-form-item>


                    <el-form-item class="btnBox">
