<script>
import Qs from "qs";
// import { quillEditor } from 'vue-quill-editor' //调用编辑器

export default {
    data() {
        var chnName = (rule, value, callback) => {
            if (!this.ruleForm.Sname) {
                callback(new Error("请输入中文名"));
            } else {
                const reg = /^[\u4e00-\u9fa5]+$/;
                if (
                    this.ruleForm.Sname.length < 100 &&
                    reg.test(this.ruleForm.Sname)
                ) {
                    callback();
                } else {
                    return callback(new Error("请输入正确的中文名"));
                }
            }
        };

        return {
            fileList: [],
            ruleForm: {
                Sname: "",
                Sid: "",
                allergy: "",
                other: ""
            },

            rules: {
                checkName: [
                    { required: true, validator: chnName, trigger: "blur" }
                ],
                email: [
                    {
                        required: true, //是否必填
                        message: "请输入邮箱地址", //错误提示信息
                        trigger: "blur" //检验方式（blur为鼠标点击其他地方，）
                    },
                    {
                        type: "email", //要检验的类型（number，email，date等）
                        message: "请输入正确的邮箱地址",
                        trigger: ["blur", "change"] //（change为检验的字符变化的时候）
                    }
                ],
                idNumber: [
                    {
                        required: true,
                        message: "请输入身份证号",
                        trigger: "blur"
                    },
                    {
                        pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
                        message: "请输入合法身份证号",
                        trigger: "blur"
                    }
                ],
                phone: [
                    {
                        required: true,
                        message: "请输入手机号",
                        trigger: "blur"
                    },
                    // 这个只能验证手机号
                    // { pattern:/^0{0,1}(13[0-9]|15[7-9]|153|156|18[7-9])[0-9]{8}$/, message: "请输入合法手机号", trigger: "blur" }
                    {
                        pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
                        message: "请输入合法手机号/电话号",
                        trigger: "blur"
                    }
                ]
            },
            editorOption: {
                placeholder: "请输入",
                theme: "snow",
                modules: {}
            }
        };
    },

    methods: {
        onSubmit() {
            let data = {
                photo: this.file,
                Sname: this.ruleForm.Sname,
                Sid: this.ruleForm.Sid,
                allergy: this.ruleForm.allergy,
                other: this.ruleForm.other,
                result: this.ruleForm.result
            };
            // console.log(this.file[0].name)
            this.$axios.post("/addsickness", Qs.stringify(data))
        },
        
        handleExceed(files, fileList) {
            this.$message.warning(
                `当前限制选择 3 个文件，本次选择了 ${
                    files.length
                } 个文件，共选择了 ${files.length + fileList.length} 个文件`
            );
        },
        beforeRemove(file) {
            return this.$confirm(`确定移除 ${file.name}？`);
        },
        onEditorChange() {},
        //文件上传
        getFile(e) {
            // this.file = event.target.files;
            // console.log(this.file)
            let file = e.target.files[0];
            let param = new FormData(); //创建form对象
            param.append("tweetPic", file, file.name); //通过append向form对象添加数据
            // console.log(param.get('tweetPic'));

            this.$axios.post("/addsickness", param)
        }
    },
    created() {
        this.author = sessionStorage.getItem("my_id");
        if ("path" in this.$route.query) {
            // path = _this.$route.query.path;

            this.ruleForm.Sname = this.$store.state.sick.addsickness[0].Sname;
            this.ruleForm.Sid = this.$store.state.sick.addsickness[0].Sid;
            // console.log(this.$store.state.sick.addsickness)
        }
    }
};
</script>

