<template>
    <div style="padding:20px;">
        <h1 class="title">患者添加</h1>

        <div>
            <div class="form-box">
                <el-form
                    :model="ruleForm"
                    status-icon
                    :rules="rules"
                    ref="ruleForm"
                    label-width="100px"
                    class="demo-ruleForm"
                >
                    <el-form-item label="姓名" prop="checkName">
                        <el-input type="text" v-model="ruleForm.realname" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="年龄" prop="checkUser">
                        <el-input type="text" v-model="ruleForm.age" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="证件号" prop="idNumber">
                        <el-input type="text" v-model="ruleForm.idNumber" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="性别">
                        <el-radio-group v-model="ruleForm.sex">
                            <el-radio label="男"></el-radio>
                            <el-radio label="女"></el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="籍贯">
                        <el-cascader
                            class="address"
                            :options="optionsjiguan"
                            v-model="form.city"
                            @change="handleChange"
                        ></el-cascader>
                    </el-form-item>

                    <el-form-item label="民族">
                        <el-input type="text" v-model="ruleForm.minzu" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="过敏病史">
                        <el-input type="text" v-model="ruleForm.allergy" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="重要疾病">
                        <el-input type="text" v-model="ruleForm.imsick" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="婚姻情况">
                        <el-radio-group v-model="ruleForm.marry">
                            <el-radio label="未婚"></el-radio>
                            <el-radio label="已婚"></el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="居住地">
                        <el-cascader
                            class="address"
                            :options="options"
                            v-model="form.livecity"
                            @change="handleChange2"
                        ></el-cascader>
                    </el-form-item>

                    <el-form-item label="家庭住址">
                        <el-input type="text" v-model="form.address" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话" prop="phone">
                        <el-input type="text" v-model="ruleForm.phone" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="其他">
                        <el-input type="text" v-model="ruleForm.other" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item class="btnBox">
                        <el-button class="btn" type="primary" @click="onSubmit">表单提交</el-button>
                        <!-- <el-button class="btn" @click="$router.push('/login')">返回</el-button> -->
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import {
    regionData,
    CodeToText,
    provinceAndCityData
} from "element-china-area-data";
import Qs from "qs";
export default {
    data() {
        var chnName = (rule, value, callback) => {
            if (!this.ruleForm.realname) {
                callback(new Error("请输入中文名"));
            } else {
                const reg = /^[\u4e00-\u9fa5]+$/;
                if (
                    this.ruleForm.realname.length < 100 &&
                    reg.test(this.ruleForm.realname)
                ) {
                    callback();
                } else {
                    return callback(new Error("请输入正确的中文名"));
                }
            }
        };

        return {
            // nameList:[],
            username: "",
            ruleForm: {
                realname: "",
                age: "",
                minzu: "",
                idNumber: "",
                sex: "男",
                allergy: "",
                imsick: "",
                marry: "未婚",
                other: "",
                phone: ""
            },
            rules: {
                checkName: [
                    { required: true, validator: chnName, trigger: "blur" }
                ],
                email: [
                    {
                        required: true, //是否必填
                        message: "请输入邮箱地址", //错误提示信息
                        trigger: "blur" //检验方式（blur为鼠标点击其他地方，）
                    },
                    {
                        type: "email", //要检验的类型（number，email，date等）
                        message: "请输入正确的邮箱地址",
                        trigger: ["blur", "change"] //（change为检验的字符变化的时候）
                    }
                ],
                idNumber: [
                    {
                        required: true,
                        message: "请输入身份证号",
                        trigger: "blur"
                    },
                    {
                        pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
                        message: "请输入合法身份证号",
                        trigger: "blur"
                    }
                ],
                phone: [
                    {
                        required: true,
                        message: "请输入手机号",
                        trigger: "blur"
                    },
                    // 这个只能验证手机号
                    // { pattern:/^0{0,1}(13[0-9]|15[7-9]|153|156|18[7-9])[0-9]{8}$/, message: "请输入合法手机号", trigger: "blur" }
                    {
                        pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
                        message: "请输入合法手机号/电话号",
                        trigger: "blur"
                    }
                ]
            },

            options: regionData,
            optionsjiguan: provinceAndCityData,

            form: {
                idcard: "",
                address: "",
                city: "",
                livecity: "",
                tel: "",
                mail: ""
            }
        };
    },
    methods: {
        onSubmit() {
            if (this.ruleForm.realname == "") {
                alert("姓名不能为空");
            } else if (this.ruleForm.idNumber == "") {
                alert("证件号不能为空");
            } else {
                let _this = this;
                let address = this.livecity + this.form.address;
                let data = {
                    Sname: this.ruleForm.realname,
                    idcard: this.ruleForm.idNumber,
                    age: this.ruleForm.age,
                    minzu: this.ruleForm.minzu,
                    Sresult: "",
                    allergy: this.ruleForm.allergy,
                    imsick: this.ruleForm.imsick,
                    sex: this.ruleForm.sex,
                    tel: this.ruleForm.phone,
                    jiguan: this.city,
                    address: address,
                    marryed: this.ruleForm.marry,
                    other: this.ruleForm.other,
                    author: this.author
                };

                this.$axios.post("/sickeradd", Qs.stringify(data)).then(res => {
                    _this.$message({
                        message: `提交成功，${res.data.Sname}的信息已添加`,
                        type: "success"
                    });
                    //清空数据
                    this.ruleForm.realname = "";
                    this.ruleForm.idNumber = "";
                    this.ruleForm.age = "";
                    this.ruleForm.minzu = "";
                    this.ruleForm.allergy = "";
                    this.ruleForm.imsick = "";
                    // this.ruleForm.sex = '';
                    this.ruleForm.phone = "";
                    this.city = "";
                    // this.ruleForm.marry = '';
                    this.livecity = "";
                    this.form.address = "";
                    this.ruleForm.other = "";
                    this.author = "";
                });
            }
        },
        handleChange(value) {
            // console.log(CodeToText[value[0]])

            this.city = CodeToText[value[0]] + CodeToText[value[1]];
            if (value[2]) {
                this.city =
                    CodeToText[value[0]] +
                    CodeToText[value[1]] +
                    CodeToText[value[2]];
            }
        },
        handleChange2(value) {
            // console.log(CodeToText[value[0]])

            this.livecity = CodeToText[value[0]] + CodeToText[value[1]];
            if (value[2]) {
                this.livecity =
                    CodeToText[value[0]] +
                    CodeToText[value[1]] +
                    CodeToText[value[2]];
            }
        }
    },
    created() {
        this.author = sessionStorage.getItem("my_id");
    }
};
</script>

<style lang="scss" scoped>
.title {
    height: 30px;
    line-height: 30px;
    font-size: 25px;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: 30px;
}
.address {
    width: 300px;
}
.el-input {
    width: 300px;
}
.el-form-item {
    width: 400px;
    margin-right: 100px;
    float: left;
}
.btnBox {
    width: 800px;
    text-align: center;
    margin: 0px;
}
.btn {
    margin-top: 30px;
    width: 200px;
}
</style>