<template>
    <div style="padding:20px;">   
        <h1 class="title">用户审批</h1>

        <div class="table_container">
            <div class="search">
                <el-input
                    placeholder="请输入内容"
                    prefix-icon="el-icon-search"
                    v-model="search"
                    clearable
                ></el-input>
            </div>
            <el-table
            :data="waitList.filter(data => !search || data.realname.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%">

                <el-table-column
                    label="用户名"
                    width="180">
                    <template slot-scope="scope">
                        
                        <span >{{ scope.row.username }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                    label="真实姓名"
                    width="180">
                    <template slot-scope="scope">                       
                        <span >{{ scope.row.realname }}</span>
                    </template>
                </el-table-column>


                <el-table-column
                    label="性别"
                    width="180">
                    <template slot-scope="scope">
                        
                        <span >{{ scope.row.sex }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                    label="证件号"
                    width="180">
                    <template slot-scope="scope">
                        
                        <span >{{ scope.row.idcard }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                    label="联系电话"
                    width="180">
                    <template slot-scope="scope">
                        
                        <span >{{ scope.row.tel }}</span>
                    </template>
                </el-table-column>


                


                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">通过</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>


            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                waitList:[],
                search:"",
            }
        },
        methods: {
            handleEdit(index, row) {
                // console.log(row);
                this.waitList.splice(index,1);
                //将等候区的用户移入用户区
                this.$axios.get("/useradd",{params:{
                    username:row.username,
                    password:row.password,
                    realname:row.realname,
                    sex:row.sex,
                    idcard:row.idcard,
                    tel:row.tel,
                    qq:row.qq,
                    purview:'1'
                }}).then(()=>{
                    this.$axios.get("/delonewait",{params:{
                        username:row.username
                    }})
                })
            },
            // 删除
            handleDelete(index, row) {
                let _this = this;
                // console.log(index, row);
                this.waitList.splice(index,1);
                //删除接口
                this.$axios.get("/delonewait",{params:{
                    username:row.username
                }}).then(()=>{
                    _this.$message({
                        message: '删除成功',
                        type: 'success'
                    });

                })
            }
        },
        created(){
            this.$axios.get("/getwaitinfo").then((res)=>{
                // console.log(res.data);
                this.waitList = res.data
            })
        }
    }
</script>

<style lang="scss" scoped>
.title{
    height:30px;
    line-height: 30px;
    font-size: 25px;
    color: rgba(0,0,0,0.6);
    margin-bottom: 30px;
}
</style>