<template>
    <div style="padding:20px;">
        <div class="title">
            <h3>发布公告</h3>
        </div>
        <div class="container">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="标题">
                    <el-input v-model="form.title"></el-input>
                </el-form-item>

                <el-form-item label="活动形式">
                    <el-input type="textarea" :rows="6" v-model="form.content"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">立即发布</el-button>
                    <el-button>取消</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import Qs from "qs";
export default {
    data() {
        return {
            form: {
                title: "",
                content: ""
            },
            editorOption: {
                placeholder: "请输入",
                theme: "snow",
                modules: {}
            }
        };
    },
    methods: {
        onSubmit() {
            let author = sessionStorage.getItem("my_id");
            let data = {
                title: this.form.title,
                content: this.form.content,
                author: author
            };
            // console.log(this.file[0].name)
            this.$axios.post("/addmsg", Qs.stringify(data)).then(() => {
                // console.log(res);
                this.$confirm("是否发布公告?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                    .then(() => {
                        this.$message({
                            type: "success",
                            message: "发布成功!"
                        });
                        this.$router.push("/mymsg")
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消发布"
                        });
                    });
            });
        }
    },
    created() {}
};
</script>

<style lang="scss" scoped>
.title {
    height: 30px;
    line-height: 30px;
    font-size: 25px;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: 30px;
}
</style>