<template>
    <div style="padding:20px;">
        <div class="title">
            <h1>个人中心</h1>
        </div>

        <div class="my_box">
            <el-form label-position="right" label-width="80px">
                <el-form-item label="头像">
                    <img :src="photo" alt />
                    <!-- <input type="file" name="photo"> -->

                    <i class="el-icon-edit edit" @click="editphoto"></i>
                </el-form-item>
                <el-form-item label="用户ID" class="half_form">
                    <el-input readonly v-model="form.id"></el-input>
                </el-form-item>

                <el-form-item label="用户名" class="half_form">
                    <el-input v-model="form.username" readonly></el-input>
                </el-form-item>
                <el-form-item v-if="visible" label="密码">
                    <el-input type="password" v-model="form.password" readonly>
                        <i
                            slot="suffix"
                            title="显示密码"
                            @click="changePass('show')"
                            style="cursor:pointer;"
                            class="el-icon-view"
                        ></i>
                    </el-input>
                    <i class="el-icon-edit edit" @click="editpw"></i>
                </el-form-item>
                <el-form-item v-else label="密码">
                    <el-input type="text" v-model="form.password" readonly>
                        <i
                            slot="suffix"
                            title="隐藏密码"
                            @click="changePass('hide')"
                            style="cursor:pointer;"
                            class="el-icon-minus"
                        ></i>
                    </el-input>
                    <i class="el-icon-edit edit" @click="editpw"></i>
                </el-form-item>
                <el-form-item label="真实姓名">
                    <el-input v-model="form.realname" readonly></el-input>
                    <i class="el-icon-edit edit" @click="editRealname"></i>
                </el-form-item>

                <el-form-item label="身份证">
                    <el-input v-model="form.idcard" readonly></el-input>
                    <i class="el-icon-edit edit" @click="editIdcard"></i>
                </el-form-item>

                <el-form-item label="性别">
                    <el-input v-model="form.sex" readonly></el-input>
                    <i class="el-icon-edit edit" @click="editSex"></i>
                </el-form-item>

                <el-form-item label="联系电话">
                    <el-input v-model="form.tel" readonly></el-input>
                    <i class="el-icon-edit edit" @click="editTel"></i>
                </el-form-item>

                <el-form-item label="邮箱">
                    <el-input v-model="form.qq" readonly></el-input>
                    <i class="el-icon-edit edit" @click="editEmail"></i>
                </el-form-item>
            </el-form>
        </div>

        <el-dialog title="修改性别" :visible.sync="dialogFormVisible" width="520px">
            <el-form :model="form">
                <el-form-item label="请选择" label-width="100px">
                    <el-select v-model="form.sex" placeholder="请选择">
                        <el-option label="男" value="男"></el-option>
                        <el-option label="女" value="女"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="修改头像" :visible.sync="photovisible" width="520px">
            <form
                class="form-horizontal"
                method="POST"
                action="http://localhost:3002/editmyphoto"
                enctype="multipart/form-data"
            >
                <div class="form-group">
                    <!-- <label class="col-sm-2 control-label">头像</label> -->
                    <div class="col-sm-10">
                        <input type="file" name="photo" />
                    </div>
                    <div class="col-sm-10" style="display:none;">
                        <input type="text"  name="id" v-model="form.id" />
                    </div>
                    <div class="col-sm-10" style="display:none;">
                        <input type="text"  name="purview" v-model="my_purview" />
                    </div>

                </div>

                <div slot="footer" class="dialog-footer photo_bottom">
                    <el-button @click="photovisible = false">取 消</el-button>
                    <button type="submit" class="btn btn-default submitBtn">提交</button>
                </div>
            </form>
        </el-dialog>
    </div>
</template>

<script>
import Qs from "qs";
export default {
    data() {
        return {
            form: {
                id: "",
                username: "",
                password: "",
                realname: "",
                idcard: "",
                sex: "",
                tel: "",
                qq: ""
            },
            photo: "",
            visible: true,
            dialogFormVisible: false,
            photovisible: false
        };
    },
    methods: {
        changePass(value) {
            //判断渲染，true:暗文显示，false:明文显示
            this.visible = !(value === "show");
        },
        //更改头像
        editphoto() {
            this.photovisible = true;
        },
        sureEditPhtot(){
            
        },
        //更改密码
        editpw() {
            this.$prompt("请输入新密码", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                inputPattern: /^.{6,}$/,
                inputErrorMessage: "密码至少六位数"
            })
                .then(({ value }) => {
                    this.$message({
                        type: "success",
                        message: "修改成功"
                    });
                    this.form.password = value;
                    // console.log(value)
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        },
        //更改真实姓名
        editRealname() {
            this.$prompt("请输入真实姓名", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                inputPattern: /^[\u4E00-\u9FA5A-Za-z]+$/,
                inputErrorMessage: "请输入真实姓名"
            })
                .then(({ value }) => {
                    this.$message({
                        type: "success",
                        message: "修改成功"
                    });
                    this.form.realname = value;
                    // console.log(value)
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        },
        //修改身份证号
        editIdcard() {
            this.$prompt("请输入身份证号", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                inputPattern: /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/,
                inputErrorMessage: "请输入正确的身份证号"
            })
                .then(({ value }) => {
                    this.$message({
                        type: "success",
                        message: "修改成功"
                    });
                    this.form.idcard = value;
                    // console.log(value)
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        },
        //修改性别
        editSex() {
            this.dialogFormVisible = true;
        },
        //修改电话号码
        editTel() {
            this.$prompt("请输入电话号码", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                inputPattern: /^1(3|4|5|6|7|8|9)\d{9}$/,
                inputErrorMessage: "请输入正确的电话号码"
            })
                .then(({ value }) => {
                    this.$message({
                        type: "success",
                        message: "修改成功"
                    });
                    this.form.tel = value;
                    // console.log(value)
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        },
        //修改邮箱
        editEmail() {
            this.$prompt("请输入邮箱", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                inputPattern: /^[a-zA-Z0-9]+([-_.][a-zA-Z0-9]+)*@[a-zA-Z0-9]+([-_.][a-zA-Z0-9]+)*\.[a-z]{2,}$/,
                inputErrorMessage: "请输入正确的邮箱"
            })
                .then(({ value }) => {
                    this.$message({
                        type: "success",
                        message: "修改成功"
                    });
                    this.form.qq = value;
                    // console.log(value)
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        }
    },
    created() {
        this.form.id = sessionStorage.getItem("my_id");
        let my_purview = sessionStorage.getItem("my_purview");
        this.my_purview = my_purview;
        let data = {
            my_id: this.form.id,
            my_purview:my_purview
        };

        this.$axios.post("/getmyinfo", Qs.stringify(data)).then(res => {
            // console.log(res.data[0]);
            this.form = res.data[0];
            let urlTemp = res.data[0].photo;
            // console.log(typeof(urlTemp))
            this.photo = "/img" + urlTemp;
        });
    },
    beforeDestroy(){
        let data = {
            my_id: this.form.id,
            my_purview:this.my_purview,
            password: this.form.password,
            realname: this.form.realname,
            idcard: this.form.idcard,
            sex: this.form.sex,
            tel: this.form.tel,
            qq: this.form.qq
            
        };
        this.$axios.post("/editmy", Qs.stringify(data))


    }
};
</script>

<style lang="scss" scoped>
.my_box {
    // width: 600px;
}

.my_box .el-form-item {
    width: 800px;
    float: left;
    input {
        width: 580px;
    }
    .el-input {
        width: 90%;
    }
    button {
        float: right;
    }
    i.edit {
        font-size: 18px;
        margin-left: 10px;
        cursor: pointer;
    }
}

.el-form-item img {
    width: 65px;
    height: 65px;
}

.half_form {
    width: 50%;
    float: left;
}


.photo_bottom{
    text-align: center;
    margin-top: 20px;

    .submitBtn{
        margin-left: 20px;
        display: inline-block;
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        background: #00B2FC;
        border: 1px solid #DCDFE6;
        color: #fff;
        
        text-align: center;
        box-sizing: border-box;
        outline: 0;
        // margin: 0;
       
        transition: .1s;
        font-weight: 500;
        padding: 12px 20px;
        font-size: 14px;
        border-radius: 4px;
    }


}



</style>