<template>
    <div style="padding:20px;">
        <h1 class="title">用户管理</h1>

        <div class="table_container">
            <el-table
                :data="userList"
                style="width: 100%">
                <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="用户id:">
                        <span>{{ props.row.id }}</span>
                    </el-form-item>
                    <el-form-item label="用户名:">
                        <span>{{ props.row.username }}</span>
                    </el-form-item>
                    <el-form-item label="真实姓名:">
                        <span>{{ props.row.realname }}</span>
                    </el-form-item>
                    <el-form-item label="性别:">
                        <span>{{ props.row.sex }}</span>
                    </el-form-item>
                    <el-form-item label="证件号:">
                        <span>{{ props.row.idcard }}</span>
                    </el-form-item>
                    <el-form-item label="联系电话:">
                        <span>{{ props.row.tel }}</span>
                    </el-form-item>
                    <el-form-item label="邮箱:">
                        <span>{{ props.row.qq }}</span>
                    </el-form-item>
                    </el-form>
                </template>
                </el-table-column>
                <el-table-column
                label="用户ID"
                prop="id">
                </el-table-column>
                <el-table-column
                label="用户名"
                prop="username">
                </el-table-column>
                <el-table-column
                label="真实姓名"
                prop="realname">
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        
                        <el-button
                        size="mini"
                        type="danger"
                        @click="onDel(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                userList:null,
            }
        },
        methods:{
            onDel(index,row){
                this.$confirm('此操作将删除该用户, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });

                    // let _this = this;
                    // console.log(row);
                    this.userList.splice(index,1);

                    //删除接口
                    this.$axios.get("/deluser",{params:{
                        id:row.id
                    }})


                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            }
        },
        created(){
            this.$axios.get("/getuserinfo").then((res)=>{
                // console.log(res.data);
                this.userList = res.data;
            })
        }
    }
</script>

<style lang="scss" scoped>
.title{
    height:30px;
    line-height: 30px;
    font-size: 25px;
    color: rgba(0,0,0,0.6);
    margin-bottom: 30px;
}
.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>