<template>
    <div style="padding:20px;">

        <!-- <div class="back" @click="router.go(-1)">
            <i class="el-icon-arrow-left"></i>
            <span>返回</span>
            
        </div> -->
        <div class="main_container">
            <h1 class="title" @click="test">患者管理</h1>

            <div class="search">
                <el-input
                    placeholder="请搜索输入内容"
                    prefix-icon="el-icon-search"
                   
                    v-model="search"
                    clearable
                ></el-input>
            </div>

            <div class="table_container">
                <el-table :data="sickerList.filter(data => !search || data.Sname.toLowerCase().includes(search.toLowerCase()) || data.sex.toLowerCase().includes(search.toLowerCase()) || data.Sid.toString().toLowerCase().includes(search.toLowerCase()) )" style="width: 100%">
                    <el-table-column type="expand">
                        <template slot-scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                                <el-form-item label="患者id:">
                                    <span>{{ props.row.Sid }}</span>
                                </el-form-item>
                                <el-form-item label="姓名:">
                                    <span>{{ props.row.Sname }}</span>
                                </el-form-item>
                                <el-form-item label="年龄:">
                                    <span>{{ props.row.age }}</span>
                                </el-form-item>
                                <el-form-item label="性别:">
                                    <span>{{ props.row.sex }}</span>
                                </el-form-item>
                                <el-form-item label="证件号:">
                                    <span>{{ props.row.idcard }}</span>
                                </el-form-item>
                                <el-form-item label="婚姻状态:">
                                    <span>{{ props.row.marryed }}</span>
                                </el-form-item>
                                <el-form-item label="民族:">
                                    <span>{{ props.row.minzu }}</span>
                                </el-form-item>
                                <el-form-item label="籍贯:">
                                    <span>{{ props.row.jiguan }}</span>
                                </el-form-item>
                                <el-form-item label="居住地:">
                                    <span>{{ props.row.address }}</span>
                                </el-form-item>
                                <el-form-item label="联系电话:">
                                    <span>{{ props.row.Stel }}</span>
                                </el-form-item>
                                <el-form-item label="过敏病史:">
                                    <span>{{ props.row.allergy }}</span>
                                </el-form-item>
                                <el-form-item label="重要疾病:">
                                    <span>{{ props.row.imsick }}</span>
                                </el-form-item>
                                <el-form-item label="添加时间:">
                                    <span>{{ props.row.updatetime }}</span>
                                </el-form-item>
                                <el-form-item label="负责医生编号:">
                                    <span>{{ props.row.author }}</span>
                                </el-form-item>
                                <div class="btn_box">
                                    <el-button size="mini" type="success" @click="onAddness(props.row.Sid,props.row.Sname)">添加病理信息</el-button>
                                    <el-button size="mini"  @click="oncheck(props.row.Sname)">查看病理信息</el-button>
                                    
                                </div>
                            </el-form>
                        </template>
                    </el-table-column>
                    <el-table-column label="患者ID" prop="Sid"></el-table-column>
                    <el-table-column label="患者性名" prop="Sname"></el-table-column>
                    <el-table-column label="性别" prop="sex"></el-table-column>

                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="onEdit(scope.$index, scope.row)">修改</el-button>
                            <el-button
                                size="mini"
                                type="danger"
                                @click="onDel(scope.$index, scope.row)"
                            >删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <div class="bar" v-if="showFlag">
                <div class="top">
                    <i class="el-icon-close" @click="onClose"></i>
                </div>
                <div class="bar_content">
                    <el-form class="clearfix">
                        <el-form-item label="姓名:">
                            <el-input v-model="editList.Sname" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="年龄:">
                            <el-input v-model="editList.age" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="性别:">
                            <el-input v-model="editList.sex" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="证件号:">
                            <el-input v-model="editList.idcard" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="婚姻状态:">
                            <el-input v-model="editList.marryed" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="民族:">
                            <el-input v-model="editList.minzu" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="籍贯:">
                            <el-input v-model="editList.jiguan" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="居住地:">
                            <el-input v-model="editList.address" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="联系电话:">
                            <el-input v-model="editList.Stel" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="过敏病史:">
                            <el-input v-model="editList.allergy" type="text"></el-input>
                        </el-form-item>
                        <el-form-item label="重要疾病:">
                            <el-input v-model="editList.imsick" type="text"></el-input>
                        </el-form-item>
                    </el-form>
                </div>

                <div class="bar_bottom">
                    <el-button class="sureEdit" size="mini" type="success" @click="sureEdit">确认修改</el-button>
                    <el-button class="sureEdit" size="mini" @click="onClose">取消</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Qs from "qs";
export default {
    data() {
        return {
            // editList:null,
            sickerList: [],
            showFlag: false,
            editList:{},
            search:"",
        };
    },
    methods: {
        test(){
            let list =  this.sickerList.filter(data => { return data.Sid.toString().includes("10")
                // console.log(data.Sid)         
            })
            // console.log(list)
        },
        onDel(index, row) {
            this.$confirm("此操作将删除该用户, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "删除成功!"
                    });
                    // let _this = this;
                    // console.log(row);
                    this.sickerList.splice(index, 1);
                    //删除接口
                    this.$axios.get("/delsicker", {
                        params:{
                            id: row.Sid
                        }
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        },
        onEdit(index,row) {
            this.showFlag = true;
            // console.log(row)


            this.editList = this.sickerList[index];
            // console.log(this.editList);
        },
        sureEdit() {
            let index = this.sickerList.findIndex(
                item => item.Sid == this.editList.Sid
            );

            this.sickerList.splice(index,this.editList);
            // console.log(index);
            this.showFlag = false;
            let _this = this;
            let data = this.editList;
            this.$axios.post("/editsicker", Qs.stringify(data)).then(() => {
                _this.$message({
                    message: `修改成功`,
                    type: "success"
                });
            });
        },
        onClose() {
            this.showFlag = false;
            this.editList =null;
        },
        onAddness(id,name){
            // console.log(id,name);
            this.$store.commit("sick/addsickness",{Sid:id,Sname:name})
            this.$router.push({
                path:"/sicknessadd",
                query:{
                    path:"/sickermange"
                }
            })
        },
        oncheck(sname){
            this.$store.commit("sick/checksickness",{Sname:sname})
            this.$router.push({
                path:"/sicknessmange",
                query:{
                    path:"/sickermange"
                }
            })

        }
    },
    created() {
        let data = {
            my_id: sessionStorage.getItem("my_id")
        };
        this.$axios.post("/getsickerinfo", Qs.stringify(data)).then(res => {
            // console.log(res.data);
            this.sickerList = res.data;
            // console.log(this.sickerList)
        });
    }
};
</script>

<style lang="scss" scoped>
.clearfix::after {
    content: "";
    display: block;
    clear: both;
}

.title {
    height: 30px;
    line-height: 30px;
    font-size: 25px;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: 30px;
}
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
    
}




// 控制弹出框
.main_container {
    position: relative;
}
.bar {
    position: fixed;
    left: 345px;
    top: 124px;
    width: 800px;
    height: 421px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 10px;
    z-index: 99;
    // box-shadow:;

    .top {
        height: 20px;

        i {
            float: right;
            font-size: 20px;
            margin-top: 10px;
            margin-right: 10px;
            cursor: pointer;
        }
    }

    .bar_content {
        // width: 100%;
        padding: 20px;
        .el-form-item {
            float: left;
            margin-right: 52px;
            margin-bottom: 0px;
            // height: 30px;
        }
        .el-input {
            width: 200px;
            height: 30px;
        }

        .el-input__inner {
            height: 30px;
        }
    }

    .bar_bottom {
        text-align: center;
    }
    .el-button .sureEdit {
        height: 30px;
        width: 100px;
    }
}
.demo-table-expand .btn_box{
    width: 800px;
    
    text-align: center;
    button{
        margin-right: 30px;
    }
}
.back{
    height:30px;
    width: 100px;
    color: rgba(0,0,0,0.5);
    cursor: pointer;
    // line-height: 30px;
}

</style>